import * as React from "react";
export default class Greeting extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            feature: "Add a feature...",
        };
    }
    render() {
        const includes = this.props.including.map((feature, index) => {
            return React.createElement("li", {key: index}, feature);
        });
        const jokes = this.props.jokes.map((joke, index) => {
            return React.createElement("li", {key: index}, joke);
        });
        return (React.createElement("div", {className: "greeting"}, 
            React.createElement("h1", null, "HelloWorld"), 
            React.createElement("p", null, this.props.greeting), 
            React.createElement("ul", null, includes), 
            React.createElement("form", {action: ""}, 
                React.createElement("input", {type: "text", defaultValue: this.state.feature, onChange: (e) => {
                    this.setState({
                        // have to cast e.target as HTMLInputElement for typesafety
                        feature: e.target.value,
                    });
                }}), 
                React.createElement("input", {type: "submit", value: "Go!", onClick: (e) => {
                    // preventDefault to stop refresh on form submit
                    e.preventDefault();
                    this.props.onAddMessage(this.state.feature);
                }})), 
            React.createElement("hr", null), 
            React.createElement("p", null, "Wanna (chuck)le? How about a random Chuck Norris joke?:"), 
            React.createElement("ul", null, jokes), 
            React.createElement("form", {action: ""}, 
                React.createElement("input", {type: "submit", value: "Get Another?", onClick: (e) => {
                    e.preventDefault();
                    this.props.getAnotherJoke();
                }})
            )));
    }
}



